export default [{"title":"About","author":"Jenson","published":"2021/02/16","description":"<p>关于作者</p>\n","image":"/photos/牛顿迭代法.png","category":"关于","tags":["关于"],"id":"About","body":"<h3>关于作者</h3>\n"},{"title":"tex语言理解","author":"Jenson","published":"2015/07/05","description":"<p>对PostScript、DVI、Tex、Latex的个人理解。</p>\n","image":"/photos/TeX.png","category":"编译技术","tags":["tex","markdown"],"id":"tex系统理解","body":"<p>tex由Knuth创建，起初是使用web语言编写的，整个tex只有一个文件tex.web。</p>\n<p>web文件中同时包含了代码以及说明这些代码的文字，这些文字也可以理解为代码的注释，但Knuth本意其实是“夹叙夹议”的编写代码，所以这些文字要比通常的注释更长，也更为文学化。</p>\n<p>web语言带有两个编译器，weave和tangle。使用weave编译tex.web，可以得到一个tex.tex文件，这个就是web文件中的文字部分。使用tangle编译，会得到一个tex.p文件，是web中的代码，为pascal语言。然后用pascal编译tex.p后得到tex可执行文件，最后tex tex.tex得到文字部分对应的dvi文件。</p>\n<p>下面说一下tex、latex、dvi、postScript、pdf之间的关系。</p>\n<h2>1. PostScript和PDF</h2>\n<p>这两个都是页面描述语言（Page Description Language, PDL），也是现在的出版印刷领域常用的两种描述语言。</p>\n<p>PostScript(PS)大体上是以代码的方式生成矢量图形的语言，也包括如何载入字体等等。由于许多打印机是点阵式输出，所以需要将PostScript格式的数据转换为点阵数据，这就需要一个光栅图像处理器（Raster Image Processor, RIP），RIP可以是软件也可以是硬件，它的功能就是读入PS数据然后转换为点阵数据。所以，对于支持PS格式的打印机，需要传给打印机的就是一个PS文件。</p>\n<p>PDF格式是PS的子集，是针对网络出版的一种格式。方便了网络上传输和查看，并增加了一些交互功能。PDF可以传给支持PDF的打印机进行打印，而不需要转为PS格式。在不支持PDF的打印机上，则要先转为PS格式才能进行打印。</p>\n<p>当然，上面的内容是打印机驱动所做的工作，已经对用户屏蔽了这些过程。</p>\n<h2>2. DVI</h2>\n<p>DVI（DeVice Independent）文件是tex文件编译后的输出格式。起初Knuth实现的tex是输出到一个特定的输出设备的，然而这种方式限制了tex的使用范围，故听从了同事的建议改为输出到一个DVI格式的文件中。这个文件格式是Knuth和他的同事设计的，独立于具体的输出设备。这个文件格式类似于PS。可以通过一些工具将其转换为PS或PDF文件。</p>\n<h2>3. tex</h2>\n<p>Knuth的代码本质上是实现了一个从tex文件到dvi文件的解析器。</p>\n<p>Knuth定义了tex这个标记语言，然后定义了dvi页面描述语言，tex程序就是做了解析和转换工作，也可以说是渲染工作。</p>\n<p>tex类似于html语言，并且在描述能力上，静态页面的部分二者几乎是相同的。tex可以实现的公式排版，使用html+css也同样可以做到，例如MathJax。</p>\n<p>渲染方面，tex使用tex.p程序进行渲染，结果是可以输出到打印机的dvi文件。而html则是浏览器进行渲染，结果是可以使用显卡输出到屏幕的opengl语句。</p>\n<h2>4. latex</h2>\n<p>原始的tex语言只提供了300条语句，可以说是最为基本的300条语句。为了方便文档撰写，人们编写了许多的宏，这就类似于C语言中的宏，将许多重复性的过程封装到一个宏语句中。tex的宏由于允许分支和循环语句，所以功能十分强大，可以实现很复杂的功能。</p>\n<p>即使是看起来十分底层的plain Tex，也是包含了许多的宏定义的。</p>\n<p>latex就是众多宏中的一个。</p>\n<p>latex标记语言类似于现在流行的Markdown，只不过要比Markdown强大许多，代价是学习成本也要高许多。这主要是因为latex面向的是纸质文章、书籍的排版，而markdown则面向网络，且追求文字撰写的流畅性。</p>\n<p>latex在执行时会先进行宏替换，转换为tex文件，然后再解析。Markdown则是先转换为html语言，再在浏览器中进行解析。</p>\n"},{"title":"ubuntu或mint下的hexo环境配置","author":"Jenson","published":"2015/07/05","description":"<p>记录Ubuntu或Mint上安装Hexo的过程。</p>\n","image":"/photos/hexo.jpg","category":"日常软件","tags":["ubuntu","mint","nodejs","markdown"],"id":"ubuntu下hexo环境配置","body":"<p>mint 是基于 ubuntu 的，安装的过程一样。</p>\n<p>hexo 依赖于：</p>\n<ul>\n<li>node.js</li>\n<li>npm</li>\n</ul>\n<p>linux下安装 node.js 比windows下麻烦一些。许多人是从源代码build的，但也有一些方便的方法，例如使用nvm。</p>\n<h2>1. nvm 的安装</h2>\n<p>参考<a href=\"https://github.com/creationix/nvm\">nvm 的 github 网站</a></p>\n<p>bash下运行下面代码：</p>\n<pre><code class=\"language-bash\">curl -o- https://raw.githubusercontent.com/creationix/nvm/v0.25.4/install.sh | bash\n</code></pre>\n<p>可能需要额外运行下面命令：</p>\n<pre><code class=\"language-bash\">. ~/.nvm/nvm.sh\n</code></pre>\n<h2>2. node.js 的安装</h2>\n<p>安装完 nvm 以后，运行下面命令安装node.js：</p>\n<pre><code class=\"language-bash\">nvm install 0.12\n</code></pre>\n<h2>3. npm 的安装</h2>\n<p>参考<a href=\"https://www.npmjs.com/package/npm\">npm 的网站</a></p>\n<pre><code class=\"language-bash\">curl -L https://npmjs.org/install.sh | sh\n</code></pre>\n<h2>4. hexo 的安装</h2>\n<p><a href=\"https://hexo.io/docs/index.html\">hexo 网址</a></p>\n<pre><code class=\"language-bash\">npm install -g hexo-cli\n</code></pre>\n<p>即安装完毕。</p>\n<h2>5. 测试</h2>\n<pre><code class=\"language-bash\">hexo init blog\ncd blog\nnpm install\n</code></pre>\n<p>如果生成了一个blog文件夹，且不为空，则安装成功。</p>\n"},{"title":"开根号算法","author":"Jenson","published":"2017/09/12","description":"<p>如何使用加减乘除求出一个实数的平方根（立方根）？大体上有两种思路：二分查找法和牛顿迭代法。</p>\n","image":"/photos/牛顿迭代法.png","category":"算法","tags":["算法","面试"],"id":"开根号算法","body":"<p>如何使用加减乘除求出一个实数的平方根（立方根）？大体上有两种思路：二分查找法和牛顿迭代法。</p>\n<h2>二分查找法</h2>\n<p>这种方法的主要思想是：先确定解的范围，然后在此范围内不断的做二分查找，直到满足精度要求。</p>\n<p>设输入的实数为<span class=\"katex\"><span class=\"katex-mathml\"><math><semantics><mrow><mi>a</mi></mrow><annotation encoding=\"application/x-tex\">a</annotation></semantics></math></span><span class=\"katex-html\" aria-hidden=\"true\"><span class=\"strut\" style=\"height:0.43056em;\"></span><span class=\"strut bottom\" style=\"height:0.43056em;vertical-align:0em;\"></span><span class=\"base textstyle uncramped\"><span class=\"mord mathit\">a</span></span></span></span>，此处假定<span class=\"katex\"><span class=\"katex-mathml\"><math><semantics><mrow><mi>a</mi></mrow><annotation encoding=\"application/x-tex\">a</annotation></semantics></math></span><span class=\"katex-html\" aria-hidden=\"true\"><span class=\"strut\" style=\"height:0.43056em;\"></span><span class=\"strut bottom\" style=\"height:0.43056em;vertical-align:0em;\"></span><span class=\"base textstyle uncramped\"><span class=\"mord mathit\">a</span></span></span></span>为正数</p>\n<ul>\n<li><span class=\"katex\"><span class=\"katex-mathml\"><math><semantics><mrow><mi>a</mi><mo>≥</mo><mn>1</mn></mrow><annotation encoding=\"application/x-tex\">a\\geq 1</annotation></semantics></math></span><span class=\"katex-html\" aria-hidden=\"true\"><span class=\"strut\" style=\"height:0.64444em;\"></span><span class=\"strut bottom\" style=\"height:0.78041em;vertical-align:-0.13597em;\"></span><span class=\"base textstyle uncramped\"><span class=\"mord mathit\">a</span><span class=\"mrel\">≥</span><span class=\"mord mathrm\">1</span></span></span></span>时，<span class=\"katex\"><span class=\"katex-mathml\"><math><semantics><mrow><msqrt><mrow><mi>a</mi></mrow></msqrt></mrow><annotation encoding=\"application/x-tex\">\\sqrt{a}</annotation></semantics></math></span><span class=\"katex-html\" aria-hidden=\"true\"><span class=\"strut\" style=\"height:0.8002800000000001em;\"></span><span class=\"strut bottom\" style=\"height:1.04em;vertical-align:-0.23972em;\"></span><span class=\"base textstyle uncramped\"><span class=\"sqrt mord\"><span class=\"sqrt-sign\" style=\"top:0.03971999999999998em;\"><span class=\"style-wrap reset-textstyle textstyle uncramped\">√</span></span><span class=\"vlist\"><span style=\"top:0em;\"><span class=\"fontsize-ensurer reset-size5 size5\"><span style=\"font-size:1em;\">​</span></span><span class=\"mord textstyle cramped\"><span class=\"mord mathit\">a</span></span></span><span style=\"top:-0.72028em;\"><span class=\"fontsize-ensurer reset-size5 size5\"><span style=\"font-size:1em;\">​</span></span><span class=\"reset-textstyle textstyle uncramped sqrt-line\"></span></span><span class=\"baseline-fix\"><span class=\"fontsize-ensurer reset-size5 size5\"><span style=\"font-size:1em;\">​</span></span>​</span></span></span></span></span></span> 满足 <span class=\"katex\"><span class=\"katex-mathml\"><math><semantics><mrow><mn>1</mn><mo>≤</mo><msqrt><mrow><mi>a</mi></mrow></msqrt><mo>≤</mo><mi>a</mi></mrow><annotation encoding=\"application/x-tex\">1\\leq \\sqrt{a}\\leq a</annotation></semantics></math></span><span class=\"katex-html\" aria-hidden=\"true\"><span class=\"strut\" style=\"height:0.8002800000000001em;\"></span><span class=\"strut bottom\" style=\"height:1.04em;vertical-align:-0.23972em;\"></span><span class=\"base textstyle uncramped\"><span class=\"mord mathrm\">1</span><span class=\"mrel\">≤</span><span class=\"sqrt mord\"><span class=\"sqrt-sign\" style=\"top:0.03971999999999998em;\"><span class=\"style-wrap reset-textstyle textstyle uncramped\">√</span></span><span class=\"vlist\"><span style=\"top:0em;\"><span class=\"fontsize-ensurer reset-size5 size5\"><span style=\"font-size:1em;\">​</span></span><span class=\"mord textstyle cramped\"><span class=\"mord mathit\">a</span></span></span><span style=\"top:-0.72028em;\"><span class=\"fontsize-ensurer reset-size5 size5\"><span style=\"font-size:1em;\">​</span></span><span class=\"reset-textstyle textstyle uncramped sqrt-line\"></span></span><span class=\"baseline-fix\"><span class=\"fontsize-ensurer reset-size5 size5\"><span style=\"font-size:1em;\">​</span></span>​</span></span></span><span class=\"mrel\">≤</span><span class=\"mord mathit\">a</span></span></span></span></li>\n<li><span class=\"katex\"><span class=\"katex-mathml\"><math><semantics><mrow><mn>0</mn><mo>≤</mo><mi>a</mi><mo>≤</mo><mn>1</mn></mrow><annotation encoding=\"application/x-tex\">0\\leq a\\leq 1</annotation></semantics></math></span><span class=\"katex-html\" aria-hidden=\"true\"><span class=\"strut\" style=\"height:0.64444em;\"></span><span class=\"strut bottom\" style=\"height:0.78041em;vertical-align:-0.13597em;\"></span><span class=\"base textstyle uncramped\"><span class=\"mord mathrm\">0</span><span class=\"mrel\">≤</span><span class=\"mord mathit\">a</span><span class=\"mrel\">≤</span><span class=\"mord mathrm\">1</span></span></span></span>时，<span class=\"katex\"><span class=\"katex-mathml\"><math><semantics><mrow><msqrt><mrow><mi>a</mi></mrow></msqrt></mrow><annotation encoding=\"application/x-tex\">\\sqrt{a}</annotation></semantics></math></span><span class=\"katex-html\" aria-hidden=\"true\"><span class=\"strut\" style=\"height:0.8002800000000001em;\"></span><span class=\"strut bottom\" style=\"height:1.04em;vertical-align:-0.23972em;\"></span><span class=\"base textstyle uncramped\"><span class=\"sqrt mord\"><span class=\"sqrt-sign\" style=\"top:0.03971999999999998em;\"><span class=\"style-wrap reset-textstyle textstyle uncramped\">√</span></span><span class=\"vlist\"><span style=\"top:0em;\"><span class=\"fontsize-ensurer reset-size5 size5\"><span style=\"font-size:1em;\">​</span></span><span class=\"mord textstyle cramped\"><span class=\"mord mathit\">a</span></span></span><span style=\"top:-0.72028em;\"><span class=\"fontsize-ensurer reset-size5 size5\"><span style=\"font-size:1em;\">​</span></span><span class=\"reset-textstyle textstyle uncramped sqrt-line\"></span></span><span class=\"baseline-fix\"><span class=\"fontsize-ensurer reset-size5 size5\"><span style=\"font-size:1em;\">​</span></span>​</span></span></span></span></span></span>则满足<span class=\"katex\"><span class=\"katex-mathml\"><math><semantics><mrow><mi>a</mi><mo>≤</mo><msqrt><mrow><mi>a</mi></mrow></msqrt><mo>≤</mo><mn>1</mn></mrow><annotation encoding=\"application/x-tex\">a\\leq \\sqrt{a}\\leq 1</annotation></semantics></math></span><span class=\"katex-html\" aria-hidden=\"true\"><span class=\"strut\" style=\"height:0.8002800000000001em;\"></span><span class=\"strut bottom\" style=\"height:1.04em;vertical-align:-0.23972em;\"></span><span class=\"base textstyle uncramped\"><span class=\"mord mathit\">a</span><span class=\"mrel\">≤</span><span class=\"sqrt mord\"><span class=\"sqrt-sign\" style=\"top:0.03971999999999998em;\"><span class=\"style-wrap reset-textstyle textstyle uncramped\">√</span></span><span class=\"vlist\"><span style=\"top:0em;\"><span class=\"fontsize-ensurer reset-size5 size5\"><span style=\"font-size:1em;\">​</span></span><span class=\"mord textstyle cramped\"><span class=\"mord mathit\">a</span></span></span><span style=\"top:-0.72028em;\"><span class=\"fontsize-ensurer reset-size5 size5\"><span style=\"font-size:1em;\">​</span></span><span class=\"reset-textstyle textstyle uncramped sqrt-line\"></span></span><span class=\"baseline-fix\"><span class=\"fontsize-ensurer reset-size5 size5\"><span style=\"font-size:1em;\">​</span></span>​</span></span></span><span class=\"mrel\">≤</span><span class=\"mord mathrm\">1</span></span></span></span>\n注意到两种情况的区间是相反的。接下来只需要在这个区间进行二分查找即可。</li>\n</ul>\n<h2>牛顿迭代法</h2>\n<p>这种方法的思想是：将此问题作为求解方程的根的问题，然后使用数值计算的方法求解。</p>\n<p>具体说来，设输入实数为<span class=\"katex\"><span class=\"katex-mathml\"><math><semantics><mrow><mi>a</mi></mrow><annotation encoding=\"application/x-tex\">a</annotation></semantics></math></span><span class=\"katex-html\" aria-hidden=\"true\"><span class=\"strut\" style=\"height:0.43056em;\"></span><span class=\"strut bottom\" style=\"height:0.43056em;vertical-align:0em;\"></span><span class=\"base textstyle uncramped\"><span class=\"mord mathit\">a</span></span></span></span>，目标是求<span class=\"katex\"><span class=\"katex-mathml\"><math><semantics><mrow><mi>x</mi><mo>=</mo><msqrt><mrow><mi>a</mi></mrow></msqrt></mrow><annotation encoding=\"application/x-tex\">x = \\sqrt{a}</annotation></semantics></math></span><span class=\"katex-html\" aria-hidden=\"true\"><span class=\"strut\" style=\"height:0.8002800000000001em;\"></span><span class=\"strut bottom\" style=\"height:1.04em;vertical-align:-0.23972em;\"></span><span class=\"base textstyle uncramped\"><span class=\"mord mathit\">x</span><span class=\"mrel\">=</span><span class=\"sqrt mord\"><span class=\"sqrt-sign\" style=\"top:0.03971999999999998em;\"><span class=\"style-wrap reset-textstyle textstyle uncramped\">√</span></span><span class=\"vlist\"><span style=\"top:0em;\"><span class=\"fontsize-ensurer reset-size5 size5\"><span style=\"font-size:1em;\">​</span></span><span class=\"mord textstyle cramped\"><span class=\"mord mathit\">a</span></span></span><span style=\"top:-0.72028em;\"><span class=\"fontsize-ensurer reset-size5 size5\"><span style=\"font-size:1em;\">​</span></span><span class=\"reset-textstyle textstyle uncramped sqrt-line\"></span></span><span class=\"baseline-fix\"><span class=\"fontsize-ensurer reset-size5 size5\"><span style=\"font-size:1em;\">​</span></span>​</span></span></span></span></span></span>，因此求解其中的<span class=\"katex\"><span class=\"katex-mathml\"><math><semantics><mrow><mi>x</mi></mrow><annotation encoding=\"application/x-tex\">x</annotation></semantics></math></span><span class=\"katex-html\" aria-hidden=\"true\"><span class=\"strut\" style=\"height:0.43056em;\"></span><span class=\"strut bottom\" style=\"height:0.43056em;vertical-align:0em;\"></span><span class=\"base textstyle uncramped\"><span class=\"mord mathit\">x</span></span></span></span>也就是求解方程<span class=\"katex\"><span class=\"katex-mathml\"><math><semantics><mrow><msup><mi>x</mi><mn>2</mn></msup><mo>−</mo><mi>a</mi><mo>=</mo><mn>0</mn></mrow><annotation encoding=\"application/x-tex\">x^2-a=0</annotation></semantics></math></span><span class=\"katex-html\" aria-hidden=\"true\"><span class=\"strut\" style=\"height:0.8141079999999999em;\"></span><span class=\"strut bottom\" style=\"height:0.897438em;vertical-align:-0.08333em;\"></span><span class=\"base textstyle uncramped\"><span class=\"mord\"><span class=\"mord mathit\">x</span><span class=\"vlist\"><span style=\"top:-0.363em;margin-right:0.05em;\"><span class=\"fontsize-ensurer reset-size5 size5\"><span style=\"font-size:0em;\">​</span></span><span class=\"reset-textstyle scriptstyle uncramped\"><span class=\"mord mathrm\">2</span></span></span><span class=\"baseline-fix\"><span class=\"fontsize-ensurer reset-size5 size5\"><span style=\"font-size:0em;\">​</span></span>​</span></span></span><span class=\"mbin\">−</span><span class=\"mord mathit\">a</span><span class=\"mrel\">=</span><span class=\"mord mathrm\">0</span></span></span></span>的根。此方程是一个非线性方程。</p>\n<p>非线性方程的数值求解方法有多种，上面的二分法就是一种简单的方法，而牛顿迭代法则是一个更为快速有效的求解方法。</p>\n<p>关于牛顿法的介绍可以参见<a href=\"https://www.zhihu.com/question/20690553#answer-26396816\">此链接</a>。</p>\n<p>可以证明，对于常见的函数，牛顿迭代法一定收敛（<a href=\"https://wenku.baidu.com/view/6f2c315e79563c1ec5da71e9.html\">参见此文</a>）。</p>\n"}];